.sendMsg > Input{
  width: 78%;
  font-size: 15px;
  font-weight: 550;
  margin-left: 0, 0, -3px, 5px;
  float: left;
}

.sendMsg > Button{
  width: 14%;
  font-size: 15px; 
  font-weight: 550;
  margin: 8px 5% -10px 5%;
  max-width: 200px;
  border: solid 1px rgb(78, 86, 190) ;
  background-color: rgb(125, 211, 107);
  border-radius: 50px;
}

.signout{
  display: flex;
  justify-content: center;
  position: fixed;
  width: 100%;
  background-color: #e3e3e7;
  top: 0;
  z-index: 10;
}

.signout > Button{
  padding: 5px;
  margin: 10px;
  font-size: 16px;
  border-radius: 5px;
  font-weight: bold;
  border: solid 1px rgb(36, 4, 3) ;
  background-color: rgb(221, 93, 76);
}

img {
  border-radius: 50%;
  height: 40px;
  margin-top: -20px;
  border: 2px solid black;
}

p {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 500;
  font-size: 20px;
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
  overflow-wrap: break-word;
}

.msg {
  display: flex;
  padding: 20px 10px 0 20px;
  margin: 20px;
  border-radius: 3000px;
  box-shadow: 0 0 10px rgb(164, 164, 164);
  align-items: center;
}

.sent {
  background-color: #4f64c2;
  color: white;
  border-top-right-radius: 1000px;
  flex-direction: row-reverse;
  padding: 3px 10px 1px 10px;
  text-align: end;
  float: right;
}

.received {
  border: 1px solid rgb(204, 184, 184);
  background-color: #dac2c2;
  border-top-left-radius: 1000px;
  float: left;
}

.sendMsg {
  position: fixed;
  display: flex;
  width: 100%;
  bottom: 0;
  z-index: 1;
  border-top: 1px solid lightgray;
  margin-left: -5px;
  padding: 10px 0px 30px 10px;
  background-color: #e3e3e7;
}

.msgs {
  margin: 65px 0 80px 0;
  display: flex;
  flex-direction: column;
}


@media (max-width: 775px) {
  p {
    font-size: 20px;
  }
  .sent {
    padding: 10px 10px 0 10px;
  }
  .received {
    padding: 10px 10px 0 10px;
  }
  img {
    height: 35px;
    margin-top: -10px;
  }
  .signout > Button{
    padding: 7px;
    margin: 7px;
    font-size: 17px;
    border-radius: 10px;
    font-weight: 300;
  }
}

@media (max-width: 500px) {
  p {
    font-size: 15px;
  }
  .sent {
    padding: 7px 7px 0 7px;
  }
  .received {
    padding: 7px 7px 0 7px;
  }
  img {
    height: 30px;
    margin-top: -7px;
  }
  .signout > Button{
    padding: 7px;
    margin: 7px;
    font-size: 15px;
    border-radius: 10px;
    font-weight: 400;
  }

}